@import "global.scss";

:host {
  /**
  * @prop --sidebar-inner-padding: The padding between the sidebar container and the content.
  * @prop --sidebar-container-box-shadow: The box shadow of the sidebar container.
  * @prop --sidebar-container-border-color: The color of the border of the sidebar container.
  * @prop --sidebar-container-border: The border of the sidebar container.
  * @prop --sidebar-container-background-color: The background color of the sidebar container.
  * @prop --sidebar-container-width: The preferred width of the sidebar container.
  * @prop --sidebar-container-transition: The transition settings of the sidebar container.
  
  * @prop display: The display mode of the sidebar.
  * @prop top: The position of the sidebar from top. By default calculated from the top of the window.
  * @prop height: The height of the sidebar.
  * @prop max-width: The max width of the sidebar.
  * @prop position: The position of the sidebar. By default, overlays the content below (absolute).
  * @prop transition: The sidebar transition settings.
  * @prop z-index: The z-index of the sidebar. Default: 10.
  */
  
  --sidebar-inner-padding: #{$rwc-spacing--md} #{$rwc-spacing--xs};
  
  --sidebar-container-box-shadow: #{$rwc-box-shadow-2xl};
  --sidebar-container-border-color: #{$rwc-color-border-primary};
  --sidebar-container-border: 1px solid var(--sidebar-container-border-color);
  --sidebar-container-background-color: #{$rwc-color-foundation-white};
  --sidebar-container-width: auto;
  --sidebar-container-max-width: 20rem;
  --sidebar-container-transition: box-shadow 400ms ease-in-out;

  display: block;
  top: 0;
  height: 100%;
  max-width: var(--sidebar-container-max-width);
  position: fixed;
  transition: left 400ms ease-in-out, right 400ms ease-in-out;
  z-index: 10;
}

.container {
  height: 100%;
  overflow-x: hidden;
  overflow-y: auto;
  background-color: var(--sidebar-container-background-color);
  box-shadow: 0 0 0 0px rgb(0 0 0 / 0);
  box-sizing: border-box;
  min-height: unset;
  width: var(--sidebar-container-width);
  padding: var(--sidebar-inner-padding);
  transition: var(--sidebar-container-transition);

  &.inner-scroll {
    height: 100%;
    min-height: unset;
  }

  &.open {
    box-shadow: var(--sidebar-container-box-shadow);
  }

  &.left {
    border-right: var(--sidebar-container-border);
  }

  &.right {
    border-left: var(--sidebar-container-border);
  }
}
