import { Component, h, Element, State, Prop, Event, EventEmitter, Method, Host, FunctionalComponent, Listen } from '@stencil/core';
import { IClosable } from '../../../interfaces/actions/IClosable';
import { IOpenable } from '../../../interfaces/actions/IOpenable';
import { IToggleable } from '../../../interfaces/actions/IToggleable';
import { SidebarPositions, SidebarStates } from '../../../interfaces/components/layout/sidebar/sidebar';
import { EventManager } from '../../../utils/actions/eventManager';
import { RWCActionHelper } from '../../../utils/actions/rwcActionHelper';

@Component({
  tag: 'rwc-sidebar',
  styleUrl: 'sidebar.scss',
  shadow: true
})
export class RwcSidebar implements IOpenable, IClosable, IToggleable {
  /**
   * The reference to the host element.
   */
  @Element() host: HTMLRwcSidebarElement;
  
  /**
   * Event manager used to manage component's event subscriptions.
   */
  public eventManager: EventManager = new EventManager();

  /**
   * List of the actions available for the Sidebar.
   */
  public actions: ('open' | 'close' | 'toggle')[] = ['open', 'close', 'toggle'];

  /**
   * The event called on the opening of the sidebar.
   */
  @Event({ eventName: 'sidebarOpen' }) openEvent: EventEmitter<void>;
  
  /**
   * The event called on the closing of the sidebar.
   */
  @Event({ eventName: 'sidebarClose' }) closeEvent: EventEmitter<void>;

  /**
   * The current open state of the sidebar.
   */
  @State() isOpen: boolean = false;

  //#region Validation

  /**
   * Default sidebar's state
   */
  @Prop() defaultState: SidebarStates = 'closed';

  /**
   * Key used for initializing event listeners
   */
  @Prop({ reflect: true }) rwcKey = RWCActionHelper.getDefaultComponentKey('sidebar');

  /**
   * Window side to stick to
   */
  @Prop({ reflect: true }) position: SidebarPositions = 'left';

  /**
   * If content is longer than sidebar then scroll self instead of parent
   */
  @Prop({ reflect: true }) innerScroll: boolean = true;

  //#endregion

  /**
   * Handles the resize event of the window.
   */
  @Listen('resize', { target: 'window' })
  onWindowResize(): void {
    this.updateHostOffset();
  }

  /**
   * Initializes default state
   */
  componentWillLoad(): void {
    this.isOpen = this.defaultState === 'open';
  }

  /**
   * Initializes event listeners on insert into DOM
   */
  componentDidLoad(): void {
    this.init();
  }

  /**
   * Removes registered event listeners before removing from DOM
   */
  disconnectedCallback(): void {
    this.eventManager.clearEventListeners();
  }

  /**
   * Initializes toggle / close / open events
   */
  @Method()
  async init(): Promise<void> {
    this.eventManager.clearEventListeners();
    this.addToggleEventListeners();
    this.updateHostOffset();
  }

  /**
   * Toggles sidebar's open state
   */
  @Method()
  async toggle(): Promise<void> {
    if (this.isOpen) {
      this.close();
    } else {
      this.open();
    }
  }

  /**
   * Opens sidebar
   */
  @Method()
  async open(): Promise<void> {
    this.isOpen = true;
    this.updateHostOffset();
    this.openEvent.emit();
  }

  /**
   * Closes sidebar
   */
  @Method()
  async close(): Promise<void> {
    this.isOpen = false;
    this.updateHostOffset();
    this.closeEvent.emit();
  }

  /**
   * Updates the left or right offset of the host element based on current state.
   */
  private updateHostOffset(): void {
    if (this.isOpen) {
      if (this.position === 'left') {
        this.host.style.right = '';
        this.host.style.left = '0';
      }

      if (this.position === 'right') {
        this.host.style.right = '0';
        this.host.style.left = '';
      }
    } else {
      let hostWidth = this.host.offsetWidth;
      if (typeof hostWidth === 'undefined') {
        hostWidth = window.innerWidth; 
      }
      
      if (this.position === 'left') {
        this.host.style.right = '';
        this.host.style.left = `-${ hostWidth }px`;
      }

      if (this.position === 'right') {
        this.host.style.right = `-${ hostWidth }px`;
        this.host.style.left = '';
      }
    }
  }

  /**
   * Find elements by attribute name and component's key and add event listeners on click
   */
  addToggleEventListeners(): void {
    RWCActionHelper.bindRWCActions(this, this.host, this.actions, this.rwcKey)
  }

  /**
   * Renders the component.
   */
  render(): FunctionalComponent {
    return (
      <Host
        class={ {
          open: this.isOpen,
          [this.position]: true,
        } }
      >
        <div
          class={ {
            container: true,
            open: this.isOpen,
            [this.position]: true,
            'inner-scroll': this.innerScroll
          } }
        >
          <slot></slot>
        </div>
      </Host>
    );
  }
}
